
.email{
	width: 100%;

	position: relative;
}
.email img{
	width: 100%;
}
.email .email-input{
	position: absolute;
	top: 30%;
	width: 50%;

	left: 5%;
}
.email-input p{
	font-size: 40px;
	font-family: simhei;
}
.email-input .email-p{
	font-size: 15px;
	font-style: italic;
	line-height: 40px;
}
.email-input p span{
	font-size: 40px;
	color: orange;
}
.email-text{
	width: 90%;
	height: 45px;
	margin-top: 10px;
	background-color: white;
	color: #8492A6;
	outline: none;
	font-size: 20px;
	text-indent: 20px;
	border: none;
}
.email-submit{
	position: absolute;
	top: 73%;
	right: 15%;
	font-size: 30px;
	color: #D3DCE6;
}
.email-submit span{
	font-size: 15px;
	margin-left: 50px;
	font-family: "arial narrow";
	color: #1F2D3D;
	vertical-align: middle;
}
.email-submit span:hover{
	color: orange;
}
@media only screen and (min-width: 768px) and (max-width: 1082px) {
.email-input .email-p{
	font-size: 12px;
	line-height: 20px;
}
.email-input p span{
	font-size: 20px;
	color: orange;
}
.email-input p{
	font-size: 20px;
	font-family: simhei;
}
.email-submit{
	font-size: 20px;
}
}
@media only screen  and (max-width: 768px) {
.email .email-input{
	top: 15%;
}
.email-input .email-p{
	display: none;
}
.email-input p span{
	font-size: 20px;
	color: orange;
}
.email-input p{
	font-size: 15px;
	font-family: simhei;
}
.email-submit{
	font-size: 12px;
}
.email-submit span{
	font-size: 12px;
	margin-left: 10px;
}
.email-text{
	height: 30px;
	margin-top: 0;
}
}



