
.blog[data-v-0792dea2]{
	width: 85%;
	max-width: 1400px;
	margin: 10px auto;
	overflow: hidden;
	position: relative;
}
.blog:hover .right[data-v-0792dea2]{
	opacity: 0.7;
}
.blog:hover .left[data-v-0792dea2]{
	opacity: 0.7;
}
.blog-show[data-v-0792dea2]{
	overflow: hidden;
	position: relative;
	width: 90%;
	height: 100%;
	margin: 0 auto;
}
.blog-move[data-v-0792dea2]{
	width: 200%;
	overflow: hidden;
	position: relative;
}
.blog-content[data-v-0792dea2]{
 	float: left;
 	width: 25%;
 	padding: 2.5% 1.25%;
}
@media only screen  and (max-width: 767px) {
.blog-move[data-v-0792dea2]{
	width: 400%;
}
}
.blog-content .blog-img[data-v-0792dea2]{
	width: 100%;
	overflow: hidden;
}
.blog-content img[data-v-0792dea2]{
	width: 100%;
	height: 100%;
	transition: all 0.5s;
}
.blog-content img[data-v-0792dea2]:hover{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.blog-text[data-v-0792dea2]{
	margin-top: 10px;
}
.blog-title[data-v-0792dea2]{
	font-family: "arial narrow";
	width: 70%;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	font-size: 20px;
}
.el-row[data-v-0792dea2]{
	margin-top: 20px;
}
.blog-time[data-v-0792dea2]{
	color: #999;
}
.blog-btn a[data-v-0792dea2]{
	text-decoration: none;
	color: #1F2D3D;
	transition: all 0.5s;
}
.left[data-v-0792dea2]{
	position: absolute;
	top: 50%;
	-webkit-transform: translate(0,-50%);
	        transform: translate(0,-50%);
	opacity: 0;
	transition: all 0.5s;
}
.right[data-v-0792dea2]{
	position: absolute;
	top: 50%;
	right: 0;
	-webkit-transform: translate(0,-50%);
	        transform: translate(0,-50%);
	opacity: 0;
	transition: all 0.5s;
}
.blog-btn a[data-v-0792dea2]:hover{
	color: orange;
}
.el-button[data-v-0792dea2]:hover{
	color: orange;
}
@media only screen and (min-width: 767px) and (max-width: 1060px) {
.blog-time[data-v-0792dea2],.blog-btn[data-v-0792dea2]{
		font-size: 12px;
}
}



