
.nav[data-v-b9a2cc40]{
	position:absolute;
	top: 70px;
	left: 50px;
	width: 95%;	
	z-index: 2;
}
.bigmenu[data-v-b9a2cc40]{
	overflow: hidden;
}
.bigmenu .logo[data-v-b9a2cc40]{
	float: left;
}
.bigmenu .menu[data-v-b9a2cc40]{
	width: 800px;
	height: 35px;
	float: right;
	font-size: 13px;
}
.bigmenu .menu span[data-v-b9a2cc40]{
	line-height: 35px;
	margin-right: 40px;
	font-family:arial;
}
.bigmenu .menu span a[data-v-b9a2cc40]{
	text-decoration: none;
	color:black;
	transition: all 0.5s;
}
.bigmenu .menu span a[data-v-b9a2cc40]:hover{
	color: orange;
}
.username[data-v-b9a2cc40]{
	margin-left: 5px;
	color: orange;
}
.select[data-v-b9a2cc40]{
	background-color: yellow;
}
.smallmenu .smalllogo[data-v-b9a2cc40]{
	float: right;
	margin-right: 20px;
	margin-top: 10px;
}
.el-button[data-v-b9a2cc40]{
	float:right;
	margin-right: 20px;
	margin-top: 10px;
	background-color: yellow;
	border: none;
}
.small-menu[data-v-b9a2cc40]{
	padding-left: 20px;
	line-height: 50px;
	font-size: 25px;
	font-family: "arial";
}
.menu-item[data-v-b9a2cc40]{
	background-color: rgb(248,248,248);
	
	text-indent: 20px;
	font-size: 15px;
	border: 1px solid #D3DCE6;
	line-height: 40px;
}
.menu-item a[data-v-b9a2cc40]{
	transition: all 0.5s;
	text-decoration: none;
	color: rgb(102,102,102);
}
.menu-item a[data-v-b9a2cc40]:hover{
	color: orange;
}
.dropdown[data-v-b9a2cc40]{
	overflow: hidden;
	width: 100%;
}
.bigmenu .menu .foucs[data-v-b9a2cc40]{
	color: orange;
}
@media only screen  and (max-width: 960px) {
.nav[data-v-b9a2cc40]{
	position: initial;
	width: 95%;	
	z-index: 2;
	margin: 10px auto;
}
}



